<template>
  <a-card class="main-container" hoverable>
    <a-typography-title :level="4">随机密码生成器</a-typography-title>
    <p>
      <a-input v-model:value="password" placeholder="随机密码(8-14位，不包括14位)" ref="passwordElement" addonBefore="密码是：" />
    </p>
    <p>
      <a-input v-model:value="passLength" placeholder="密码位数" addonBefore="密码位数：" disabled />
    </p>
    <div class="btn-container">
      <a-button type="primary" @click="createPassword">生成密码</a-button>&nbsp;
      <a-button type="primary" @click="copyPassword">复制密码</a-button>
    </div>
  </a-card>
</template>
<script setup>
import { onMounted, ref } from "vue";
import { message } from 'ant-design-vue';

const password = ref("");
const passwordElement = ref(null);
const passLength = ref(0);

// 生成随机位数、生成随机密码
const createPassword = () => {
  password.value = "";
  const chars = "0123456789abcdefghijklmnopqrstuvwxyz!@#$%^&*()ABCDEFGHIJKLMNOPQRSTUVWXYZ";
  passLength.value = Math.floor(Math.random() * 6 + 8);//8-14之间的一个随机数,不包括14
  for (let i = 0; i < passLength.value; i++) {
    const index = Math.floor(Math.random() * chars.length); //Math.floor(Math.random()*24)表示结果为0-23间的随机数
    password.value += chars.substring(index, index + 1);
  }
  console.log(password.value);
}

// 选中并复制
const copyPassword = () => {
  passwordElement.value.select();// 执行选中操作
  passwordElement.value.setSelectionRange(0, -1);// 全选
  document.execCommand("copy");// 文档，执行复制操作
  message.success('复制成功');
}

onMounted(() => { });
</script>
<style lang="less" scoped>
.main-container {
  width: 500px;
  margin: 150px auto;

  h4.ant-typography {
    text-align: center;
  }

  .btn-container {
    text-align: center;
  }
}
</style>